import Helper from '@/function.js';
export default {
	data() {
		return {
			token: "",
			list: []

		}


	},
	onLoad: function() {
		this.token = uni.getStorageSync('token');
		this.getList();
	},
	onShow: function() {
		uni.removeStorageSync("bank")
	},
	methods: {
		goBack(){
			uni.navigateBack({
				delta:1
			})
		},
		getList() {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/MemberGetBankAccountList',
				data: {
					pageIndex: 1,
					pageSize: 100,
				},
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {
						_this.list = res.data.list
					} else {

					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}


			})
		},

		selectBank(item) {
			uni.setStorageSync("bank", item)
			uni.navigateBack({
				delta: 1
			})

		}


	}


}
